@media (min-width: 1920px){ 

.languageSettingsButton{

    box-sizing: border-box;

    position: absolute;
    height: 37px;
    width: 61px;
    left: 1500px;
    top: 76px;
    border-radius: 10px;
    
    
    border: 1px solid #FFFFFF;
    border-radius: 10px;
    cursor: pointer;

}

.languageSettingsText{
/* Eng */


position: absolute;
width: 37px;
height: 23px;
left: 10px;
top: -8px;

font-family: 'Raleway';
font-style: normal;
font-weight: 400;
font-size: 20px;
line-height: 23px;
text-align: center;

color: #FFFFFF;
cursor: pointer;

}


.Vector {
    position: absolute;
    width: 79px;
    height: 129px;
    border-radius: 10px;
    border: 1px solid #FFF;
    left: 40%; /* Переместить в центр страницы */
    top: 350px; /* Переместить в центр страницы */
    transform: translate(-50%, -50%); /* Центрировать элемент */
    z-index: 999; /* Назначить высокий уровень z-index, чтобы элемент находился выше других элементов */
    margin-left: 787px;
    margin-top: -240px;
}
.Eng {
    color: #FFF;
text-align: center;
font-size: 20px;
font-family: Raleway;
cursor: pointer;

}
.VectorTwo{
    width: 47.5px;
    margin-left: 15px;
border-top: 2px solid #FFFF;

}
.Rus {
    color: #FFF;
text-align: center;
font-size: 20px;
font-family: Raleway;
cursor: pointer;

}
.AppHebrew{
    color: #FFF;
text-align: center;
font-size: 20px;
font-family: Raleway;
cursor: pointer;

}
}

