@media (min-width: 1920px){
    .VectorInputTwo{
        position: absolute;
        width: 471px;
        left: 430.83px;
        top: 5800px;
    }
    
    .Number{
        position: absolute;
        color: white;
        font-size: 24px;
        top: 5675px;
        left: 1100px;
    }
    .stileInputTwo.VectorInputTwo[type="range"] {
        background-color: transparent;
        border: none;
    }
    
    .stileInputTwo.VectorInputTwo[type="range"]::before {
        content: "";
        position: absolute;
        top: 1px;
        height: 4px;
        background-color: #fbb03b; /* Здесь можно изменить цвет */
        transform: translateY(-50%);
    }
    
    .stileInputTwo.VectorInputTwo[type="range"]::before {
        /* ... */
        width: var(--range-width, 0);
    }
}
